import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import {withRouter} from 'react-router-dom';
import * as icon from 'react-feather';

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: { min: 8, max: 15 },
    };
  }
  handleSubmit = (event) => {
    event.preventDefault()
    localStorage.clear();
    if(event.target.elements.email.value==="sp@test.com"){
      this.nextPath('/ServiceProviders')
      localStorage.setItem('role', true);
    }
    if(event.target.elements.email.value==="admin@test.com"){
      localStorage.setItem('role', false)
      this.nextPath('/dashboard')

    }
    console.log(event.target.elements.email.value)
  }
  nextPath(path) {
    this.props.history.push(path);
  }
  render() {
    return (
      <div>
        <meta charSet="utf-8" />
        <title>Pick&amp;Drop</title>
        {/* Begin page */}
        <div className="account-pages my-5">
          <div className="container">
            <div className="row justify-content-center">
              <div className="col-xl-10">
                <div className="card">
                  <div className="card-body p-0">
                    <div className="row">
                      <div className="col-md-6 p-5">
                        <div className="mx-auto mb-5">
                          <img src="assets/images/logo-dark.svg" alt="" height={50} />
                        </div>
                        <h6 className="h5 mb-0 mt-4">Welcome back!</h6>
                        <p className="text-muted mt-1 mb-4">Enter your email address and password to access admin panel.</p>
                        <form action="#" className="authentication-form" onSubmit={this.handleSubmit}>
                          <div className="form-group">
                            <label className="form-control-label">Email Address</label>
                            <div className="input-group input-group-merge">
                              <div className="input-group-prepend">
                                <span className="input-group-text">
                                <icon.Mail className="icon-dual" ></icon.Mail>   
                                </span>
                              </div>
                              <input type="email" className="form-control" id="email" placeholder="user name" />
                            </div>
                          </div>
                          <div className="form-group mt-4">
                            <label className="form-control-label">Password</label>
                            <a href="forgot-password.html" className="float-right text-muted text-unline-dashed ml-1">Forgot your password?</a>
                            <div className="input-group input-group-merge">
                              <div className="input-group-prepend">
                                <span className="input-group-text">
                                <icon.Lock className="icon-dual" ></icon.Lock>
                                </span>
                              </div>
                              <input type="password" className="form-control" id="password" placeholder="Enter your password" />
                            </div>
                          </div>
                          <div className="form-group mb-4">
                            <div className="custom-control custom-checkbox">
                              <input type="checkbox" className="custom-control-input" id="checkbox-signin" defaultChecked />
                              <label className="custom-control-label" htmlFor="checkbox-signin">Remember me</label>
                            </div>
                          </div>
                          <div className="form-group mb-0 text-center">
                            <button className="btn btn-primary btn-block" type="submit"> Log In </button>
                          </div>
                        </form>
                      </div>
                      <div className="col-lg-6 d-none d-md-inline-block">
                        <div className="auth-page-sidebar">
                        </div>
                      </div>
                    </div>
                  </div> {/* end card-body */}
                </div>
                {/* end card */}
                <div className="row mt-3">
                  <div className="col-12 text-center">
                    <p className="text-muted">Don't have an account?
                      <Link className="text-primary font-weight-bold ml-1" to='/register'>
                        <span> Register </span>
                      </Link></p>
                  </div> {/* end col */}
                </div>
                {/* end row */}
              </div> {/* end col */}
            </div>
            {/* end row */}
          </div>
          {/* end container */}
        </div>
        {/* end page */}
        {/* END wrapper */}
        {/* Vendor js */}
        {/* App js */}
      </div>
    );
  }
}

export default withRouter(Login);

