import AddBox from '@material-ui/icons/AddBox';
import ArrowDownward from '@material-ui/icons/ArrowDownward';
import Check from '@material-ui/icons/Check';
import ChevronLeft from '@material-ui/icons/ChevronLeft';
import ChevronRight from '@material-ui/icons/ChevronRight';
import Clear from '@material-ui/icons/Clear';
import DeleteOutline from '@material-ui/icons/DeleteOutline';
import Edit from '@material-ui/icons/Edit';
import FilterList from '@material-ui/icons/FilterList';
import FirstPage from '@material-ui/icons/FirstPage';
import LastPage from '@material-ui/icons/LastPage';
import Remove from '@material-ui/icons/Remove';
import SaveAlt from '@material-ui/icons/SaveAlt';
import Search from '@material-ui/icons/Search';
import ViewColumn from '@material-ui/icons/ViewColumn';
import MaterialTable from "material-table";
import React, { Component, forwardRef } from 'react';
import { Modal } from 'react-bootstrap';
import * as Icon from 'react-feather';
import { withRouter } from 'react-router-dom';
const tableIcons = {
  Add: forwardRef((props, ref) => <AddBox {...props} ref={ref} />),
  Check: forwardRef((props, ref) => <Check {...props} ref={ref} />),
  Clear: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
  Delete: forwardRef((props, ref) => <DeleteOutline {...props} ref={ref} />),
  DetailPanel: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
  Edit: forwardRef((props, ref) => <Edit {...props} ref={ref} />),
  Export: forwardRef((props, ref) => <SaveAlt {...props} ref={ref} />),
  Filter: forwardRef((props, ref) => <FilterList {...props} ref={ref} />),
  FirstPage: forwardRef((props, ref) => <FirstPage {...props} ref={ref} />),
  LastPage: forwardRef((props, ref) => <LastPage {...props} ref={ref} />),
  NextPage: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
  PreviousPage: forwardRef((props, ref) => <ChevronLeft {...props} ref={ref} />),
  ResetSearch: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
  Search: forwardRef((props, ref) => <Search {...props} ref={ref} />),
  SortArrow: forwardRef((props, ref) => <ArrowDownward {...props} ref={ref} />),
  ThirdStateCheck: forwardRef((props, ref) => <Remove {...props} ref={ref} />),
  ViewColumn: forwardRef((props, ref) => <ViewColumn {...props} ref={ref} />)
};

class Voucher extends Component {
  constructor(props) {
    super(props);
    this.state = { open: false }
  }

  handleClose = () => this.setState({ open: false });
  handleShow = () =>{
    this.setState({ open: true });}
  render() {
    return (
      <div><MaterialTable icons={tableIcons}
        title="Vouchers"
        columns={[
          { title: 'Voucher Number', field: 'sp' },
          { title: 'Voucher Amount', field: 'count' },
          { title: 'Voucher Status', field: 'status' },
          { title: 'Availed By', field: 'email' }


        ]}
        data={[
          { sp: '1', count: '500',status:"Availed" ,email:"abc.tect.com"},
          { sp: '2', count: '1000',status:"Available" ,email:"abc.tect.com"},
        ]}
        actions={[
          {
            icon: Icon.Send,
            tooltip: 'Edit City',
            onClick: (event, rowData) =>{ this.handleShow() }
          },
        ]}
        options={{
          headerStyle: {
            backgroundColor: '#01579b',
            color: '#FFF'
          },
          exportButton: true,
          actionsColumnIndex: -1

        }}
      />
        {/* Add New Event MODAL */}
        <Modal className="modal fade" id="add-modal" tabIndex={-1} show={this.state.open} onHide={() => this.handleClose()}>
          <div className="modal-header py-3 px-4 border-bottom-0 d-block">
            <button type="button" className="close" data-dismiss="modal" aria-hidden="true" onClick={() => this.handleClose()}>×</button>
            <h5 className="modal-title" id="modal-title">Voucher</h5>
          </div>
          <div className="modal-body p-4">
            <form className="needs-validation" name="event-form" id="form-event" noValidate>
              <div className="row">
                <div className="col-12">
                  {/* <th>Pick Up location</th>
                                                        <th>Drop location</th>
                                                        <th>No Of Passengers</th>
                                                        <th>PickUp time</th>
                                                        <th>Drop time</th>
                                                        <th>Return trip</th>
                                                        <th>Vehicle</th>
                                                        <th>Amount</th>
                                                        <th>Message</th> */}

                   <div className="form-group">
                    <label className="control-label">Send To</label>
                    <input className="form-control" placeholder="User Email" type="text" required />
                    <div className="invalid-feedback">Please provide a valid Email</div>
                  </div>

                </div>
              </div>
              <div className="row mt-2">
                <div className="col-12 text-right">
                  <button type="button" className="btn btn-light mr-1" data-dismiss="modal" onClick={(data) => { this.handleClose() }}>Cancel</button>
                  <button type="submit" className="btn btn-success" id="btn-save-event" onClick={(data) => { this.handleClose() }}>Save</button>
                </div>
              </div>
            </form>
          </div>
        </Modal >
        {/* end modal*/}

      </div>)
      ;
  }
}
export default withRouter(Voucher)
